import uniIcon from "@/components/uni/uni-icon/uni-icon.vue"
export default {
	data() {
		return {
			id:'',
			edit:true,
			body_height: 640,
			from:{username:'',bank_num:'',bank_name:''}
		};
	},
	onLoad(option) {  
		if(option.type=='add'){
			this.edit=false
			uni.removeStorageSync('bank')
		}
		const form=uni.getStorageSync('bank')
		if(form){
			this.from=form
		}
		this.id=form.id
	},
	components: {
		uniIcon
	},
	methods:{
		tijiao(){ 
			// this.$api.http.post('shop_cms/add_card',this.from).then(res=>{
				this.$api.msg('操作成功')
				setTimeout(()=>{
					uni.navigateTo({
						url:'/pages/cms/user/fenxiao/tixian/tixian'
					});
				},1000)
			// })
		},
		delet(){
		
			const that=this
			uni.showModal({
				title:'是否删除？',
				success(res) { 
					if(res.confirm){
						// that.$api.http.get('shop_cms/del_card?id='+that.id).then(res=>{
							that.$api.msg('操作成功')
							setTimeout(()=>{
								uni.navigateTo({
									url:'/pages/cms/user/fenxiao/tixian/tixian'
								});
							},1000)
						// })
					}
				}
			})
		},
		edits(){
			// this.$api.http.post('shop_cms/edit_card',this.from).then(res=>{
				this.$api.msg('操作成功')
				setTimeout(()=>{
					uni.navigateTo({
						url:'/pages/cms/user/fenxiao/tixian/tixian'
					});
				},1000)
			// })
		}
	}
}
